<template>
  <div id="app" class="flex-column">
    <nav v-if="mode === 'normal'" class="navbar pure-menu pure-menu-horizontal flex-row">
      <ul class="pure-menu-list">
        <li
        v-for="tab in tabs"
        :key="tab.name"
        class="pure-menu-item"
        >
        <router-link :to="tab.route" class="pure-menu-link">{{ tab.name }}</router-link>
        </li>
      </ul>
      <ul class="pure-menu-list ml-auto">
        <li v-if="login" class="pure-menu-item">
          <router-link :to="'/profile/' + user.username" class="inline-block pure-menu-link">个人信息</router-link>
        </li>
        <li v-if="login" class="pure-menu-item">
          <a href="#" class="inline-block pure-menu-link" @click="toLogout">退出</a>
        </li>
        <li v-else class="pure-menu-item">
          <router-link to="/Login" class="pure-menu-link">登陆</router-link>
        </li>
      </ul>
    </nav>
    <router-view 
      class="relative flex-1"
    />
</div>
</template>

<script>

export default {
  data() {
    return {
      mode: window.$mode || 'normal',
      login: false,
      user: {},

      tabs: [
        {
          name: '测试',
          route: '/tests',
        },
        {
          name: '卡片',
          route: '/cards',
        },
        {
          name: '请求',
          route: 'requests',
        }
      ],
    };
  },
  mounted() {
    let user = localStorage.getItem('user');
    if (user) {
      user = JSON.parse(user);
      this.login = true;
      this.user = user;
      window.$user = user;
      const jwt = localStorage.getItem('jwt');
      this.$axios.defaults.headers.common['Authorization'] = `Bearer ${jwt}`;
    }
    this.$bus.$on('changeMode', this.toChangeMode);
    this.$bus.$on('login', this.toLogin);
  },
  destroyed() {
    this.$bus.$off('changeMode', this.toChangeMode);
    this.$bus.$off('login', this.toLogin);
  },
  methods: {
    toChangeMode(mode) {
      this.mode = mode;
      localStorage.setItem('mode', mode);
    },
    toLogin() {
      this.login = true;
      this.user = window.$user;
    },
    toLogout() {
      // forget user
      localStorage.clear();
      this.login = false;
      this.$axios.defaults.headers.common['Authorization'] = '';
    },
  },
};
</script>

<style lang="scss" scoped>
#app {
  background: #eee;
  min-height: 100vh;
  min-width: 900px;
  .navbar {
    background: black;
  }
}
</style>